.sectionWrapper {}

.sectionTitle {
    @apply text-5xl font-acumin-pro font-bold mb-2;
}

.projectWrapper {
    @apply grid gap-7 mt-8;
    @apply sm:grid-cols-2 sm:gap-7;
    @apply md:grid-cols-3 md:gap-6;
    @apply lg:grid-cols-4 lg:gap-4;
}

.projectCard {
    @apply cursor-pointer py-3 border-t my-4 border-t-eco-grey;
}

.projectDirectory {
    @apply text-eco-grey uppercase font-medium tracking-wide text-[11px] mb-2;
}

.projectCardTitle {
    @apply tracking-wide font-acumin-pro text-eco-white text-2xl;
}

.projectCard:hover .projectCardTitle {
    @apply underline; 
}

.theBadge {
    @apply bg-eco-purple pl-2 w-36 h-28 flex flex-col justify-start;
}

.badgeIcon {
    @apply text-7xl text-eco-white;
}

.badgeText {
    @apply text-xs text-eco-white pl-6;
}

.allBadges{
    @apply flex flex-row space-x-1;
}